import React from "react";

function Carousel() {
  return (
    <div
      id="carouselExampleControls"
      className="carousel1 carousel slide"
      data-bs-ride="carousel"
    >
      <div className="carousel-inner">
        <div className="carousel-item active">
          <img
            src="https://www.blackngreen.com/assets/img/sliders/slide-2.jpg"
            className="d-block w-100 img-fluid"
            alt="Image1"
          />
          <div className="carousel-caption carousel1-caption d-block">
            <h5>Among the fastest-growing</h5>
            <h1>
              Mobile Technology
              <br />
              Companies in the world.
            </h1>
          </div>
        </div>
        <div className="carousel-item">
          <img
            src="https://www.blackngreen.com/assets/img/sliders/slide-3.jpg"
            className="d-block w-100 img-fluid"
            alt="Image2"
          />
          <div className="carousel-caption carousel1-caption d-block">
            <h5>Touching Billions</h5>
            <h1>
              by resolving their
              <br />
              Mobile technology needs.
            </h1>
          </div>
        </div>
        <div className="carousel-item">
          <img
            src="https://www.blackngreen.com/assets/img/sliders/slide-0.jpg"
            className="d-block w-100 img-fluid"
            alt="Image3"
          />
          <div className="carousel-caption carousel1-caption d-block">
            <h5>Creating Technologies</h5>
            <h1>
              in step with the
              <br />
              Global Telecom Industry.
            </h1>
          </div>
        </div>
      </div>
      <button
        className="carousel-control-prev"
        type="button"
        data-bs-target="#carouselExampleControls"
        data-bs-slide="prev"
      >
        <span className="carousel-control-prev-icon" aria-hidden="true"></span>
        <span className="visually-hidden">Previous</span>
      </button>
      <button
        class="carousel-control-next"
        type="button"
        data-bs-target="#carouselExampleControls"
        data-bs-slide="next"
      >
        <span className="carousel-control-next-icon" aria-hidden="true"></span>
        <span className="visually-hidden">Next</span>
      </button>
    </div>
  );
}

export default Carousel;
