import React from "react";

function Testimonial() {
  return (
    <div className="testimonials">
      <div class="d-flex flex-column align-items-center wow fadeInUp">
        <h6 class="font-weight-bold text-main">Celebrating 100</h6>
        <h3 class="font-weight-bold text-center mx-lg-4">
          <span class="d-block shadow-bottom center">
            Partners, Customers, Fans
          </span>
        </h3>
        <span class="line center my-3"></span>
      </div>
      <div class="container">
        <div
          id="carouselExampleControls1"
          class="carousel carousel2 slide text-center"
          data-bs-ride="carousel"
        >
          <div class="carousel-inner">
            <div class="carousel-item active">
              <video
                src="https://www.blackngreen.com/assets/videos/orange.mp4"
                controls
                class="img-fluid rounded-lg"
              ></video>
              <div class="align-items-center">
                <img
                  src="https://www.blackngreen.com/assets/img/company/orange.png"
                  alt="orange"
                  width="64"
                />
              </div>
            </div>
            <div class="carousel-item">
              <video
                src="https://www.blackngreen.com/assets/videos/unitel.mp4"
                controls
                class="img-fluid rounded-lg"
              ></video>
              <div class="align-items-center">
                <img
                  src="https://www.blackngreen.com/assets/img/company/unitel_cape_verdae.png"
                  alt="orange"
                  width="64"
                />
              </div>
            </div>
            <div class="carousel-item">
              <video
                src="https://www.blackngreen.com/assets/videos/xl.mp4"
                controls
                class="img-fluid rounded-lg"
              ></video>
              <div class="align-items-center">
                <img
                  src="https://www.blackngreen.com/assets/img/company/xl_indonesia.png"
                  alt="orange"
                  width="64"
                />
              </div>
            </div>
          </div>
          <button
            class="carousel-control-prev"
            type="button"
            data-bs-target="#carouselExampleControls1"
            data-bs-slide="prev"
          >
            <span class="carousel-control-prev-icon" aria-hidden="true"></span>
            <span class="visually-hidden">Previous</span>
          </button>
          <button
            class="carousel-control-next"
            type="button"
            data-bs-target="#carouselExampleControls1"
            data-bs-slide="next"
          >
            <span class="carousel-control-next-icon" aria-hidden="true"></span>
            <span class="visually-hidden">Next</span>
          </button>
        </div>
      </div>
    </div>
  );
}

export default Testimonial;
