import React from "react";
import Button from "./smallComponents/Button";

function NavBar() {
  return (
    <nav className="navbar navbar-expand-lg">
      <div className="container-fluid">
        <img
          className="logo"
          src="https://www.blackngreen.com/assets/img/logo.png"
          alt="bg"
        />
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div
          className="collapse buttons-all navbar-collapse"
          id="navbarSupportedContent"
        >
          <button className="buttons active">Home</button>
          <Button name="About" />
          <Button name="Products" />
          <Button name="MA Platform" />
          <Button name="Customers" />
          <Button name="Careers" />
          <Button name="Contact Us" />
        </div>
      </div>
    </nav>
  );
}

export default NavBar;
