import React from "react";

function FootPrint2() {
  return (
    <div className="foot-print2 container my-5">
      <div className="row">
        <div className="col-12">
          <h3 className="font-weight-bold text-center">
            <span className="shadow-bottom center">Our Global Footprint </span>
          </h3>
          <span className="d-none d-lg-block line center my-3"></span>
          <p className="text-center">
            Reaching the farthest corners of the world!
          </p>
        </div>
        <div className="foot2-img text-center col-12">
          <img
            src="https://www.blackngreen.com/assets/img/map_home.png"
            alt="map"
            className="img-fluid"
          />
        </div>
      </div>
    </div>
  );
}

export default FootPrint2;
